import React from 'react';
import "./IndrestedProduct.css"
function Product() {
  return (
    <div className='Product'>
      <div className="main">
        <div className="product-image-div">
          <img className='image' src="data:image/jpeg;base64," alt="" />
        </div>
        <div className="details">
          <h5 className="name">iPhone i4 pro max</h5>
          <div className="prices">
            <h4 className="cartopedia-price">100000</h4>
            <h4 className="mrp">140000</h4>
          </div>
          <h5 className="description">ip68 water proof . Latest samsung . Best phone</h5>
          <div className="end-items">
            <h6 className="trending">Indrested</h6>
            <h6 className="company">samsung electronics ltd</h6>
          </div>
        </div>
      </div>
    </div>
  );
};
export default Product;
